export default {
  namespaced: true,
  state: () => ({
    electron: {
      isFocused: false,
      window: null,
    },
  }),
  mutations: {
    setElectronWindow (state, window) {
      console.log('mutation setElectronWindow');
      state.electron.window = window;
    },
    setElectronFocus (state, isFocused) {
      state.electron.isFocused = isFocused;
    },
  },
  actions: {
  },
  getters: { },
};
