</template>

<style>

</style>

<script>

export default {
  name: 'Clinic',

  components: {

  },

  data: () => ({
    searchText: null,

    patients: [
      {name: 'ELIETE DE FATIMA SANTOS', date: '09/12/2020', time: '12:30', qst: false, focus: false},
      {name: 'MIRELE MARTINS DE ABREU', date: '09/12/2020', time: '12:40', qst: true, focus: false},
      {name: 'ELISABETH ROCHA SANTOS', date: '09/12/2020', time: '12:50', qst: false, focus: false},
      {name: 'MARILENE JESUS DOS SANTOS', date: '09/12/2020', time: '13:00', qst: true, focus: false},
      {name: 'CELIA VIANA DA COSTA', date: '09/12/2020', time: '13:10', qst: false, focus: false},
      {name: 'ELIETE DE FATIMA SANTOS', date: '09/12/2020', time: '12:30', qst: false, focus: false},
      {name: 'MIRELE MARTINS DE ABREU', date: '09/12/2020', time: '12:40', qst: true, focus: false},
      {name: 'ELISABETH ROCHA SANTOS', date: '09/12/2020', time: '12:50', qst: false, focus: false},
      {name: 'MARILENE JESUS DOS SANTOS', date: '09/12/2020', time: '13:00', qst: true, focus: false},
      {name: 'CELIA VIANA DA COSTA', date: '09/12/2020', time: '13:10', qst: false, focus: false},
      {name: 'ELIETE DE FATIMA SANTOS', date: '09/12/2020', time: '12:30', qst: false, focus: false},
      {name: 'MIRELE MARTINS DE ABREU', date: '09/12/2020', time: '12:40', qst: true, focus: false},
      {name: 'ELISABETH ROCHA SANTOS', date: '09/12/2020', time: '12:50', qst: false, focus: false},
      {name: 'MARILENE JESUS DOS SANTOS', date: '09/12/2020', time: '13:00', qst: true, focus: false},
      {name: 'CELIA VIANA DA COSTA', date: '09/12/2020', time: '13:10', qst: false, focus: false},
    ],
  }),

  computed: {
    electronIsFocused () {
      return this.$store.state.clinic.electron?.isFocused;
    },
  },

  watch: {
    electronIsFocused () {
      this.updateElectronWindowSize();
    },
  },

  methods: {

    patientFit () {
      this.$router.push({ name: 'clinic.patient.fit' });
    },

    showSurvey () {
      this.$router.push({ name: 'clinic.patient.survey' });
    },

    showPatient () {
      this.$router.push({ name: 'clinic.patient.show' });
    },

    updateElectronWindowSize () {
      let electronWindow = this.$store.state.clinic.electron?.window;
      let electronIsFocused = this.$store.state.clinic.electron?.isFocused;
      if (electronWindow) {
        if (electronIsFocused ) {
          electronWindow.setMinimumSize(550, 370);
          electronWindow.setSize(550, 370);
        } else {
          electronWindow.setMinimumSize(300, 105);
          electronWindow.setSize(300, 105);
        }
      }
    },
  },

  created () {
  },

  mounted () {
    this.updateElectronWindowSize();
  },
};

</script>
