import User from '@/apis/user';
import { SET_PATIENT } from './user-mutation-types';

export default {
  namespaced: true,
  state: () => ({
    patient: null,
  }),
  mutations: {
    [SET_PATIENT] (state, patient) {
      state.patient = patient;
    },
  },
  actions: {
    me: ({ commit }) => {
      return new Promise((resolve, reject) => {
        User.me()
          .then(response => {
            commit(SET_PATIENT, response.data.data);
            resolve(response);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
  },
  getters: {
    loggedIn: state => {
      return state.patient !== null;
    },
    patient: state => state.patient,
    patient_avatar: (state) => {
      if (state.patient && state.patient.socials && state.patient.socials.length > 0) {
        return state.patient.socials[0].avatar;
      }
      return null;
    },
  },
};
