<template>
  <component :is="layout">
    <transition name="slide" mode="out-in">
      <router-view/>
    </transition>
  </component>
</template>

<script>

import moment from 'moment';

export default {
  computed: {
    layout () {
      return `${this.$route.meta.layout || 'default'}-layout`;
    },
  },

  mounted () {
    const locale = (window.navigator.userLanguage || window.navigator.language).toLowerCase();
    moment.locale(locale);
    this.addElectronEvents();
  },

  methods: {
    addElectronEvents() {
      let remote = window.electron?.remote;
      if (remote) {
        let currentWindow = remote.getCurrentWindow();
        this.$store.commit('clinic/setElectronWindow', currentWindow);

        currentWindow.on('focus', () => {
          this.$store.commit('clinic/setElectronFocus', true);
        });

        currentWindow.on('blur', () => {
          this.$store.commit('clinic/setElectronFocus', false);
        });
      }
    },
  },

};
</script>

<style>

  .slide-enter-active,
  .slide-leave-active {
    transition: opacity 0.2s, transform 0.2s;
  }

  .slide-enter,
  .slide-leave-to {
    opacity: 0;
    transform: translateX(-30%);
  }

  .fade-enter-active,
  .fade-leave-active {
    transition: opacity 0.3s;
  }

  .fade-enter,
  .fade-leave-to {
    opacity: 0;
  }

</style>
