<template>
  <div>
    <ul v-for="schedule in schedules" :key="schedule.uuid">
      <li class="border-t border-gray-200">
        <router-link :to="{ name: 'user.schedules.view', params: { uuid: schedule.uuid } }" class="flex items-center block hover:bg-gray-50 focus:outline-none focus:bg-gray-50 transition duration-150 ease-in-out">
          <div class="px-4 py-4 sm:px-6 flex-1">
            <div class="flex items-center justify-between">
              <div class="sm:flex">
                <div class="mr-6 flex items-center text-sm leading-5 text-gray-500">
                  {{ schedule.speciality }}
                </div>
              </div>
              <div class="mt-2 flex items-center text-sm leading-5 text-gray-500 sm:mt-0">
                {{ schedule.procedure }}
              </div>
            </div>
            <div class="mt-2 sm:flex sm:justify-between">
              <div class="text-sm leading-5 font-medium text-indigo-600 truncate">
                {{ schedule.appointment_date_for_human }} - {{ moment(schedule.appointment_date).format('L') }} {{ schedule.appointment_time }}
              </div>
              <div v-if="schedule.appointment_status != 'attended'" class="flex-shrink-0 flex">
                <span class="px-2 inline-flex text-xs leading-5 font-semibold rounded-full bg-red-400 text-white">
                  {{ getAppointmentStatus(schedule.appointment_status) }}
                </span>
              </div>
            </div>
          </div>
          <div class="mx-2">
            <svg class="h-5 w-5 text-gray-400" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
              <path fill-rule="evenodd" d="M7.293 14.707a1 1 0 010-1.414L10.586 10 7.293 6.707a1 1 0 011.414-1.414l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0z" clip-rule="evenodd" />
            </svg>
          </div>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>

import moment from 'moment';

export default {
  props: ['schedules'],
  data: () => ({
    moment,
  }),
  methods: {
    getAppointmentStatus (status) {
      if (status === 'standby') {
        return 'Não compareceu';
      } else if (status === 'received') {
        return 'Recepcionado';
      } else if (status === 'screened') {
        return 'Triado';
      } else if (status === 'attended') {
        return 'Atendido';
      } else if (status === 'absent') {
        return 'Ausente';
      } else if (status === 'inactivated') {
        return 'Inativado';
      } else if (status === 'completed') {
        return 'Concluído';
      }
    },
  },
};

</script>
