<template>

  <div class="px-2">

    <div class="mt-2">
      <span class="pl-4 font-bold text-lg text-blue-800">Questionário: Ultrassonagrafia</span>
    </div>

    <div class="relative flex items-stretch flex-grow my-3 ">
      <input
        class="text-sm pl-16 rounded-md shadow h-10 pr-9 block w-full rounded transition ease-in-out duration-150 sm:leading-5"
        v-model="patient.name"
      />
      <div class="text-sm pl-2 absolute inset-y-0 left-2 pr-3 flex items-center pointer-events-none">
        <span class="text-xs">Nome:</span>
      </div>
      <div class="absolute inset-y-0 right-0 pr-3 flex items-center cursor-pointer">
        <svg class="h-5 w-5 text-gray-400" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
          <path fill-rule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clip-rule="evenodd" />
        </svg>
      </div>
    </div>

    <div class="flex flex-col bg-white shadow rounded-md px-4 overflow-y-scroll" style="height: 500px">
      <div class="flex flex-col my-3 pb-6 border-b" v-for="(question, index) in questions" :key="index">
        <span>{{index+1}} - {{question.label}}</span>
        <div class="flex items-center mt-2" v-for="(answer, index) in question.answers" :key="index">
          <input type="checkbox" readonly class="form-checkbox h-4 w-4 text-indigo-600 transition duration-150 ease-in-out" v-model="answer.selected">
          <span class="ml-2 text-sm">{{answer.label}}</span>
        </div>
      </div>
    </div>

  </div>


</template>

<style>

</style>

<script>

export default {
  name: 'Clinic',

  components: {

  },

  data: () => ({
    showTable: true,

    questions: [
      {
        label: 'Quão satifeito(a) você esta com sua saude?',
        answers: [
          { label: 'Satifeito', selected: false },
          { label: 'Nem insatisfeito, nem satisfeito', selected: true },
          { label: 'Insatisfeito', selected: false },
          { label: 'Muito satisfeito', selected: false },
          { label: 'Muito insatisfeito', selected: false },
        ],
      },

      {
        label: 'E por fim, conte para nós, o que é mais importante para você melhorar na sua saúde hoje?',
        answers: [
          { label: 'Ter meus exames médicos em dia', selected: false },
          { label: 'Tratar de uma dor ou doença especifica', selected: false },
          { label: 'Alimentação mais saúdavel', selected: false },
          { label: 'Reduzir stress e ansiedade', selected: false },
          { label: 'Iniciar uma atividade física/esporte', selected: true},
          { label: 'Melhorar qualidade de sono', selected: false },
          { label: 'Emagrecer', selected: false },
        ],
      },

    ],

    patient: {
      name: 'ELIETE DE FATIMA SANTOS',
      date: '09/12/2020',
      time: '12:30',
      qst: false,
      focus: false,
      phone: null,
      doctor: null,
      procedure: null,
      cpf: '458.475.256-56',
      address: 'Rua Tiradente, 1458',
      cep: '13789-575',
    },
  }),

  computed: {
    electronIsFocused () {
      return this.$store.state.clinic.electron?.isFocused;
    },
  },

  watch: {
    electronIsFocused () {
      this.updateElectronWindowSize();
    },
  },

  methods: {

    updateElectronWindowSize () {
      let electronWindow = this.$store.state.clinic.electron?.window;
      let electronIsFocused = this.$store.state.clinic.electron?.isFocused;
      if (electronWindow) {
        if (electronIsFocused) {
          electronWindow.setMinimumSize(350, 650);
          electronWindow.setSize(450, 650);
        } else {
          electronWindow.setMinimumSize(350, 650);
          electronWindow.setSize(350, 650);
        }
      }
    },

  },

  mounted () {
    this.updateElectronWindowSize();
  },

};

</script>
