<template>
  <div>

    <p class="text-sm leading-5 font-medium text-gray-700">
      Escanear código QR
    </p>
    <div class="w-full py-2">
      <qrcode-stream @decode="onDecode" @init="onInit" class="w-full" />
      <p class="error">{{ error }}</p>
    </div>
    <div class="mt-6 flex items-center justify-end w-full">
      <div class="text-sm leading-5">
        <router-link :to="{name: 'login'}">
          <a href="#" class="font-medium text-indigo-600 hover:text-indigo-500 focus:outline-none focus:underline transition ease-in-out duration-150">
            Voltar
          </a>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>

import { QrcodeStream } from 'vue-qrcode-reader';
import { mapActions } from 'vuex';

export default {
  name: 'LoginQrcode',

  components: {
    QrcodeStream,
  },

  data: () => ({
    result: '',
    error: '',
  }),

  computed: {
  },

  methods: {
    ...mapActions({
      authLoginQrcode: 'auth/loginQrcode',
    }),

    onDecode (result) {
      console.log('onDecode qrcode', result);
      const loginQrcodeUrl = process.env.VUE_APP_FRONT_URL + '/login/qrcode';
      const idx = result.lastIndexOf(loginQrcodeUrl);
      const urlLength = (loginQrcodeUrl).length;
      if (idx > -1) {
        const uuid = result.substring(idx + urlLength + 1);
        this.authLoginQrcode({ qrcode_uuid: uuid })
          .then(() => {
            this.$router.push({ name: 'user.dashboard' });
          })
          .catch(error => {
            console.error('login error', error);
          });
      } else {
        this.result = 'QRCode invalid: ' + result;
      }
    },

    async onInit (promise) {
      try {
        await promise;
      } catch (error) {
        if (error.name === 'NotAllowedError') {
          this.error = 'ERROR: you need to grant camera access permisson';
        } else if (error.name === 'NotFoundError') {
          this.error = 'ERROR: no camera on this device';
        } else if (error.name === 'NotSupportedError') {
          this.error = 'ERROR: secure context required (HTTPS, localhost)';
        } else if (error.name === 'NotReadableError') {
          this.error = 'ERROR: is the camera already in use?';
        } else if (error.name === 'OverconstrainedError') {
          this.error = 'ERROR: installed cameras are not suitable';
        } else if (error.name === 'StreamApiNotSupportedError') {
          this.error = 'ERROR: Stream API is not supported in this browser';
        }
      }
    },
  },
  mounted () {
    const qrcodeUuid = this.$route.params.uuid;
    if (qrcodeUuid) {
      this.authLoginQrcode({ qrcode_uuid: qrcodeUuid })
        .then(() => {
          this.$router.push({ name: 'user.dashboard' });
        })
        .catch(error => {
          console.error('login error', error);
        });
    }
  },
};

</script>

