import Auth from '@/apis/auth';
import { SET_PATIENT } from './user-mutation-types';
import router from '@/router';

export default {
  namespaced: true,
  state: () => ({
  }),
  mutations: {
  },
  actions: {
    login: ({ commit }, userData) => {
      return new Promise((resolve, reject) => {
        Auth.login(userData)
          .then(response => {
            commit(`user/${SET_PATIENT}`, response.data.data, { root: true });
            resolve(response);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    loginQrcode: ({ commit }, qrcodeData) => {
      return new Promise((resolve, reject) => {
        Auth.loginQrcode(qrcodeData)
          .then(response => {
            commit(`user/${SET_PATIENT}`, response.data.data, { root: true });
            resolve(response);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    loginUserData: ({ commit }, data) => {
      return new Promise((resolve, reject) => {
        Auth.loginUserData(data)
          .then(response => {
            commit(`user/${SET_PATIENT}`, response.data.data, { root: true });
            resolve(response);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    loginToken: ({ commit }, token) => {
      return new Promise((resolve, reject) => {
        Auth.loginToken(token)
          .then(response => {
            commit(`user/${SET_PATIENT}`, response.data.data, { root: true });
            resolve(response);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    logout: ({ commit }) => {
      return new Promise((resolve, reject) => {
        Auth.logout()
          .then(response => {
            commit(`user/${SET_PATIENT}`, null, { root: true });
            router.push({ name: 'login' });
            resolve(response);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
  },
  getters: { },
};
