<script>

export default {
  name: 'Clinic',

  components: {

  },

  data: () => ({
    showTable: true,
    patient: {
      name: 'ELIETE DE FATIMA SANTOS',
      phone: '(19) 99187-2595',
      date: '20/11/2020',
      time: '12:30',
      qst: false,
      doctor: 'DR. JOSE DA SILVA OLIVEIRA',
      procedure: 'ULTRASSONOGRAFIA',
      cpf: '458.475.256-56',
      address: 'Rua Tiradente, 1458',
      cep: '13789-575',
    },
  }),

  computed: {
    electronIsFocused () {
      return this.$store.state.clinic.electron?.isFocused;
    },
  },

  watch: {
    electronIsFocused () {
      this.updateElectronWindowSize();
    },
  },

  methods: {

    updateElectronWindowSize () {
      let electronWindow = this.$store.state.clinic.electron?.window;
      let electronIsFocused = this.$store.state.clinic.electron?.isFocused;
      if (electronWindow) {
        if (electronIsFocused ) {
          electronWindow.setMinimumSize(400, 570);
          electronWindow.setSize(400, 570);
        } else {
          electronWindow.setMinimumSize(400, 225);
          electronWindow.setSize(400, 255);
        }
      }
    },

  },

  mounted () {
    this.updateElectronWindowSize();
  },

};

</script>
