<script>

import LoginUserPassword from "@/components/Auth/LoginUserPassword";
import FacebookButton from '@/components/SocialLogin/FacebookButton';
import GoogleButton from '@/components/SocialLogin/GoogleButton';
import InstagramButton from '@/components/SocialLogin/InstagramButton';
import { mapActions } from 'vuex';

export default {
  name: 'Login',

  components: {
    FacebookButton,
    GoogleButton,
    InstagramButton,
    LoginUserPassword,
  },

  data: () => ({
  }),

  computed: {
  },

  methods: {
    ...mapActions({
      authLoginToken: 'auth/loginToken',
      userMe: 'user/me',
    }),

  },

  mounted () {
    const token = this.$route.params.token;
    if (token) {
      this.authLoginToken(token)
        .then(() => {
          this.$router.push({ name: 'user.dashboard' });
        })
        .catch(error => {
          console.error('login error', error);
        });
    }
  },
};

