<template>
  <div>

    <form method="post" @submit.prevent="doSearchUser" class="mt-6">
      <div>
        <label for="user_code" class="block text-sm font-medium leading-5 text-gray-700">CPF, Celular ou Usuário</label>
        <div class="mt-1 flex rounded-md shadow-sm">
          <div class="relative flex items-stretch flex-grow focus-within:z-10">
            <imask-input
                id="user_code"
                required
                v-model="form.user_code"
                :mask="maskForUserCodePhoneCpf"
                :unmask="true"
                class="form-input pr-9 block w-full rounded-none rounded-l-md transition ease-in-out duration-150 sm:text-sm sm:leading-5"
            />
            <div class="absolute inset-y-0 right-0 pr-3 flex items-center cursor-pointer">
              <svg v-if="form.user_code" @click="clearUserCode" class="h-5 w-5 text-gray-400" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
                <path fill-rule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clip-rule="evenodd" />
              </svg>
            </div>
          </div>
          <button class="w-14 -ml-px relative inline-flex items-center px-4 py-2 border border-gray-300 text-sm leading-5 font-medium rounded-r-md text-gray-700 bg-gray-50 hover:text-gray-500 hover:bg-white focus:outline-none focus:shadow-outline-blue focus:border-blue-300 active:bg-gray-100 active:text-gray-700 transition ease-in-out duration-150">
            <div v-if="busy" class="absolute right-2">
              <svg class="animate-spin h-5 w-5 mx-2 text-gray" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
                <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
              </svg>
            </div>
            <svg v-else class="h-5 w-5 text-gray-400" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
              <path fill-rule="evenodd" d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z" clip-rule="evenodd" />
            </svg>
          </button>
        </div>
      </div>
      <p v-if="error" class="mt-2 text-sm text-red-600" id="email-error">Informação inválida</p>
    </form>

    <fieldset v-if="options" class="mt-6">
      <legend class="sr-only">
        Opções de recuperação
      </legend>
      <p class="text-sm">Selecione uma opção</p>
      <div class="bg-white rounded-md -space-y-px mt-2">
        <div
          v-for="(option, index) in options"
          :key="index"
          :class="{
              'rounded-tl-md rounded-tr-md': index === 0,
              'rounded-bl-md rounded-br-md': index === options.length-1,
              'bg-indigo-50 border-indigo-200 z-10': selectedOption && selectedOption.value === option.value
          }"
          class="relative border border-gray-200 p-4 flex"
        >
          <div class="flex items-center h-5">
            <input :id="`settings-option-${index}`" v-model="selectedOption" :value="option" name="recovery_option" type="radio" class="form-radio h-4 w-4 text-indigo-600 transition duration-150 ease-in-out cursor-pointer">
          </div>
          <label :for="`settings-option-${index}`" class="ml-3 flex flex-col flex-1 cursor-pointer">
            <span class="block text-sm text-gray-900 leading-5 font-medium" :class="{ 'text-indigo-900': selectedOption && selectedOption.value === option.value }">
            {{ option.value }}
            </span>
            <span class="block text-sm text-gray-500 leading-5" :class="{ 'text-indigo-700': selectedOption && selectedOption.value === option.value }">
            {{ option.label }}
            </span>
          </label>
          <div class="flex items-center justify-center pointer-events-none">
            <svg v-if="option.type ==='phone'" class="h-5 w-5 text-gray-400" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
              <path d="M2 3a1 1 0 011-1h2.153a1 1 0 01.986.836l.74 4.435a1 1 0 01-.54 1.06l-1.548.773a11.037 11.037 0 006.105 6.105l.774-1.548a1 1 0 011.059-.54l4.435.74a1 1 0 01.836.986V17a1 1 0 01-1 1h-2C7.82 18 2 12.18 2 5V3z" />
            </svg>
            <svg v-if="option.type ==='social'" class="h-5 w-5 text-gray-400" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M5.121 17.804A13.937 13.937 0 0112 16c2.5 0 4.847.655 6.879 1.804M15 10a3 3 0 11-6 0 3 3 0 016 0zm6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
            </svg>
          </div>
        </div>
      </div>
      <div v-if="selectedOption" class="mt-6">
        <span class="block w-full rounded-md shadow-sm">
          <button
            @click="doLoginForgot"
            class="w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-500 focus:outline-none focus:border-indigo-700 focus:shadow-outline-indigo active:bg-indigo-700 transition duration-150 ease-in-out"
          >
            Enviar
          </button>
        </span>
      </div>
    </fieldset>

    <div v-if="showUserDataLogin" class="mt-6 relative">
      <div class="absolute inset-0 flex items-center">
        <div class="w-full border-t border-gray-300"></div>
      </div>
      <div class="relative flex justify-center text-sm leading-5">
        <span class="px-2 bg-gray-50 text-gray-500">
          OU
        </span>
      </div>
    </div>

    <LoginUserData v-if="showUserDataLogin" :cpf="form.user_code" class="mt-6" />

  </div>
</template>

<script>

import Auth from '@/apis/auth';
import { mapActions } from 'vuex';
import LoginUserData from '@/components/Auth/LoginUserData';
import { IMaskComponent } from 'vue-imask';
import maskForUserCodePhoneCpf from './auth-masks';
import { isCPF } from 'brazilian-values';

export default {
  name: 'LoginForgot',

  components: {
    LoginUserData,
    'imask-input': IMaskComponent,
  },

  data: () => ({
    form: {
      user_code: null,
    },
    options: null,
    selectedOption: null,
    error: false,
    showUserDataLogin: false,
    maskForUserCodePhoneCpf,
    busy: false,
  }),

  computed: {
  },

  methods: {
    ...mapActions({
      // authForgotSearch: 'auth/forgotSearch',
    }),

    doSearchUser () {
      this.busy = true;
      this.error = false;

      const data = {
        user_code: this.form.user_code,
      };

      Auth.forgotSearch(data)
        .then(response => {
          this.options = response.data;
          if (isCPF(this.form.user_code)){
            this.showUserDataLogin = true;
          }
          this.selectedOption = null;
          this.busy = false;
        })
        .catch(() => {
          this.options = null;
          this.selectedOption = null;
          this.busy = false;
          this.error = true;
        });
    },

    doLoginForgot () {
      if (this.selectedOption.type === 'social') {
        if (this.selectedOption.label === 'facebook') {
          window.location.href = `${process.env.VUE_APP_API_URL}/auth/login/facebook`;
        } else if (this.selectedOption.label === 'google') {
          window.location.href = `${process.env.VUE_APP_API_URL}/auth/login/google`;
        } else if (this.selectedOption.label === 'google') {
          window.location.href = `${process.env.VUE_APP_API_URL}/auth/login/instagram`;
        }
      } else if (this.selectedOption.type === 'phone') {
        alert(`Link de acesso enviado para ${this.selectedOption.value}`);
      }
    },

    clearUserCode () {
      this.showUserDataLogin = false;
      this.form.user_code = null;
      this.selectedOption = null;
      this.options = null;
      this.error = false;
    },
  },
};

</script>
