<template>
  <form method="post" @submit.prevent="doLoginUserData">

    <div v-if="!hideCpf">
      <label for="birthday" class="block text-sm font-medium leading-5 text-gray-700">
        CPF
      </label>
      <div class="mt-1 rounded-md shadow-sm">
        <imask-input
            id="cpf"
            v-model="form.cpf"
            required
            :mask="[{mask: '000.000.000-00', overwrite: true}]"
            :unmask="true"
            class="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:shadow-outline-blue focus:border-blue-300 transition duration-150 ease-in-out sm:text-sm sm:leading-5"
        />
      </div>
    </div>

    <div class="mt-6">
      <label for="birthday" class="block text-sm font-medium leading-5 text-gray-700">
        Data de Nascimento
      </label>
      <div class="mt-1 rounded-md shadow-sm">
        <imask-input
            id="birthday"
            v-model="form.birthday"
            required
            :mask="[{
              mask: 'DD/MM/YYYY',
              blocks: {
                DD: {
                  mask: IMask.MaskedRange,
                  from: 1,
                  to: 31
                },
                MM: {
                  mask: IMask.MaskedRange,
                  from: 1,
                  to: 12
                },
                YYYY: {
                  mask: IMask.MaskedRange,
                  from: 1900,
                  to: 2050
                },
              },
              lazy: true,
              overwrite: true,
            }]"
            :unmask="true"
            class="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:shadow-outline-blue focus:border-blue-300 transition duration-150 ease-in-out sm:text-sm sm:leading-5"
        />
      </div>
    </div>

    <div class="mt-6">
      <label for="mother_name" class="block text-sm font-medium leading-5 text-gray-700">
        Nome da Mãe
      </label>
      <div class="mt-1 rounded-md shadow-sm">
        <input id="mother_name" required v-model="form.mother_name" class="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:shadow-outline-blue focus:border-blue-300 transition duration-150 ease-in-out sm:text-sm sm:leading-5"
        ></div>
    </div>

    <p v-if="error" class="mt-2 text-sm text-red-600" id="email-error">Informações inválidas</p>

    <div class="mt-6">
      <span class="block w-full rounded-md shadow-sm">
        <button type="submit" class="w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-500 focus:outline-none focus:border-indigo-700 focus:shadow-outline-indigo active:bg-indigo-700 transition duration-150 ease-in-out">
          Entrar
        </button>
      </span>
    </div>

    <div class="mt-6 flex items-center justify-end w-full">
      <div class="text-sm leading-5">
        <router-link :to="{name: 'login'}">
          <a href="#" class="font-medium text-indigo-600 hover:text-indigo-500 focus:outline-none focus:underline transition ease-in-out duration-150">
            Voltar
          </a>
        </router-link>
      </div>
    </div>
  </form>
</template>

<script>

import moment from 'moment';
import { IMaskComponent } from 'vue-imask';
import { mapActions } from 'vuex';
import IMask from 'imask';

export default {

  components: {
    'imask-input': IMaskComponent,
  },

  props: [
    'social_uuid',
    'cpf',
  ],

  data: () => ({
    IMask,
    form: {
      social_uuid: null,
      code: null,
      password: null,
      cpf: null,
      birthday: null,
      mother_name: null,
    },
    hideCpf: false,
    error: false,
  }),

  computed: {
  },

  methods: {
    ...mapActions({
      authLoginUserData: 'auth/loginUserData',
    }),

    doLoginUserData () {
      this.error = false;

      let data = {
        cpf: this.form.cpf,
        birthday: moment(this.form.birthday, 'DD/MM/YYYY').format(),
        mother_name: this.form.mother_name,
      };

      if (this.social_uuid) {
        data['social_uuid'] = this.social_uuid;
      }

      this.authLoginUserData(data)
        .then(() => {
          this.$router.push({ name: 'user.dashboard' });
        })
        .catch( () => {
          this.error = true;
        });
    },
  },

  mounted () {
    this.form.social_uuid = this.$route.params.uuid;
    if (this.cpf) {
      this.form.cpf = this.cpf,
      this.hideCpf = true;
    }
  },
};

</script>
