import Schedules from '@/apis/schedule';
import { SET_SCHEDULES, SET_SCHEDULE } from './schedule-mutation-types';

export default {
  namespaced: true,
  state: () => ({
    schedules: null,
    schedule: null,
  }),
  mutations: {
    [SET_SCHEDULES] (state, data) {
      state.schedules = data;
    },
    [SET_SCHEDULE] (state, data) {
      state.schedule = data;
    },
  },
  actions: {
    list: ({ commit }, query) => {
      return new Promise((resolve, reject) => {
        Schedules.list(query)
          .then(response => {
            commit(SET_SCHEDULES, response.data.data);
            commit(SET_SCHEDULE, null);
            resolve(response);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    get: ({ commit }, uuid) => {
      return new Promise((resolve, reject) => {
        Schedules.get(uuid)
          .then(response => {
            commit(SET_SCHEDULE, response.data.data);
            resolve(response);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
  },
  getters: {
    schedules: state => state.schedules,
    schedule: state => state.schedule,
  },
};
