import IMask from 'imask';

const maskForUserCodePhoneCpf = [
    {
        mask: '###-###-###',
        definitions: {
            '#': /[A-HJKMNP-Z1-9]/,
        },
        prepare: function (str) {
            return str.toUpperCase();
        },
        overwrite: true,
    },
    {
        mask: '(DD) P 0000-0000',
        blocks: {
            DD: {
                mask: IMask.MaskedEnum,
                enum: [
                    '11', '12', '13', '14', '15', '16', '17', '18', '19',
                    '21', '22', '24', '27', '28',
                    '31', '32', '33', '34', '35',
                    '37', '38',
                    '41', '42', '43', '44', '45', '46', '47', '48', '49',
                    '51',
                    '53', '54', '55',
                    '61', '62', '63', '64', '65', '66', '67', '68', '69',
                    '71',
                    '73', '74', '75',
                    '77', '79',
                    '81', '82', '83', '84', '85', '86', '87', '88', '89',
                    '91', '92', '93', '94', '95', '96', '97', '98', '99',
                ],
            },
            P: {
                mask: IMask.MaskedEnum,
                enum: ['9'],
            },
        },
        overwrite: true,
    },
    {
        mask: '000.000.000-00',
        overwrite: true,
    },
];

export default maskForUserCodePhoneCpf;
