<template>
  <form method="post" @submit.prevent="doLogin">
    <div>
      <label for="email" class="mt-6 block text-sm font-medium leading-5 text-gray-700">
        Usuário, CPF ou Celular
      </label>
      <div class="mt-1 rounded-md shadow-sm">
        <imask-input
            id="email"
            required
            v-model="user_code"
            :mask="maskForUserCodePhoneCpf"
            :unmask="true"
            class="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:shadow-outline-blue focus:border-blue-300 transition duration-150 ease-in-out sm:text-sm sm:leading-5"
        />
      </div>
    </div>

    <div class="mt-6">
      <label for="password" class="block text-sm font-medium leading-5 text-gray-700">
        Senha
      </label>
      <div class="mt-1 rounded-md shadow-sm">
        <input id="password" v-model="user_password" type="password" required class="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:shadow-outline-blue focus:border-blue-300 transition duration-150 ease-in-out sm:text-sm sm:leading-5">
      </div>
    </div>

    <p v-if="error" class="mt-2 text-sm text-red-600" id="email-error">Informação inválida</p>

    <slot></slot>

    <div class="mt-6">
        <span class="block w-full rounded-md shadow-sm">
          <button type="submit" class="relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-500 focus:outline-none focus:border-indigo-700 focus:shadow-outline-indigo active:bg-indigo-700 transition duration-150 ease-in-out">
            <div v-if="busy" class="absolute right-0">
              <svg class="animate-spin h-5 w-5 mx-2 text-gray" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
                <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
              </svg>
            </div>
            Entrar
          </button>
        </span>
    </div>
  </form>

</template>

<script>

import { IMaskComponent } from 'vue-imask';
import maskForUserCodePhoneCpf from '@/views/auth/auth-masks';
import {mapActions} from "vuex";

export default {

  components: {
    'imask-input': IMaskComponent,
  },

  props: [
    'social_uuid',
  ],

  data: () => ({
    busy: false,
    user_code: null,
    user_password: null,
    maskForUserCodePhoneCpf,
    error: false,
  }),

  computed: {
  },

  methods: {
    ...mapActions({
      authLogin: 'auth/login',
    }),

    doLogin () {
      this.error = false;
      let data = {
        code: this.user_code,
        password: this.user_password,
      };
      if (this.social_uuid) {
        data['social_uuid'] = this.social_uuid;
      }
      this.busy = true;
      this.authLogin(data)
        .then(() => {
          this.busy = false;
          this.$router.push({ name: 'user.dashboard' });
        })
        .catch( () => {
          this.error = true;
          this.busy = false;
        });
    },
  },

  mounted () {

  },
};

</script>
