import Api from './api';
import Csrf from './csrf';

export default {
  async login (payload) {
    await Csrf.getCookie();
    return Api.post('/auth/login', payload);
  },

  async loginQrcode (payload) {
    await Csrf.getCookie();
    return Api.post('/auth/login/qrcode', payload);
  },

  async loginUserData (data) {
    await Csrf.getCookie();
    return Api.post('/auth/login/patient-data', data);
  },

  async loginToken (token) {
    await Csrf.getCookie();
    return Api.post('/auth/login/token', {}, { headers: { Authorization: `Bearer ${token}` } });
  },

  async logout () {
    await Csrf.getCookie();
    return Api.post('/auth/logout');
  },

  forgotSearch (data) {
    return Api.post('/auth/forgot/search', data);
  },
};
